import React from 'react';

import SectionTitle from 'containers/SectionTitle';

import PortfolioWrapper from './PortfolioWrapper';
import Image from './Image';
import PortfolioItem from './PortfolioItem';
import Title from './Title';
import DescriptionText from './DescriptionText';
import Description from './Description';
import DemonstrationLink from './DemonstrationLink';

export class Portfolio extends React.PureComponent {
  render() {
    return (
      <PortfolioWrapper>
        <SectionTitle
          defaultTitleValue=""
          titleName="portfolio-title"
          maxLengthValue="50"
        />

        <PortfolioItem>
          <a
            href="http://roboquasar.pe.hu/"
            aria-label="roboquasar project link"
            target="_blank"
            rel="noopener noreferrer"
          >
            <Image src="resume_screen.png" alt="resume screen" />
          </a>
          <Description>
            <Title
              type="text"
              value=" "
              name="portfolio-item-title"
            />
            <DescriptionText
              descriptionHeight="120px"
              name="portfolio-item-description"
              value="    ,      .   ''      HTML5, CSS3, JavaScript,   JQuery,  LESS   Bootstrap 3."
            />

            <DemonstrationLink
              href="http://roboquasar.pe.hu/"
              aria-label="roboquasar project link"
              target="_blank"
              rel="noopener noreferrer"
            >
              
            </DemonstrationLink>
          </Description>
        </PortfolioItem>

        <PortfolioItem>
          <a
            href="/portfolio/bootstrap/index.html"
            aria-label="bootstrap project link"
            target="_blank"
            rel="noopener noreferrer"
          >
            <Image src="bootstrap.png" alt="bootstrap screen" />
          </a>
          <Description>
            <Title
              type="text"
              value="-"
              name="portfolio-item-title"
            />
            <DescriptionText
              name="portfolio-item-description"
              value=" ,         .    Bootstrap 3."
            />
            <DemonstrationLink
              href="/portfolio/bootstrap/index.html"
              aria-label="bootstrap project link"
              target="_blank"
              rel="noopener noreferrer"
            >
              
            </DemonstrationLink>
          </Description>
        </PortfolioItem>

        <PortfolioItem>
          <a
            href="http://robocollapse.pe.hu/"
            aria-label="robocollapse project link"
            target="_blank"
            rel="noopener noreferrer"
          >
            <Image src="busin-sol_screen.png" alt="company screen" />
          </a>
          <Description>
            <Title
              type="text"
              value="   "
              name="portfolio-item-title"
            />
            <DescriptionText
              name="portfolio-item-description"
              value="    WordPress-   .      HTML5  CSS3,    CMS."
            />

            <DemonstrationLink
              href="http://robocollapse.pe.hu/"
              aria-label="robocollapse project link"
              target="_blank"
              rel="noopener noreferrer"
            >
              
            </DemonstrationLink>
          </Description>
        </PortfolioItem>
      </PortfolioWrapper>
    );
  }
}

export default Portfolio;



